<template>
  <Modal @close="closeChangePasswordModal" :modalActive="showChangePasswordModal"
    title='Modifiez votre mot de passe'>
    <div class="modal-content">
    <form @submit.prevent="handleChangePassword()">
      <InputPassword
        title="Mot de passe actuel"
        @new-value="recordCurrentPwd($event)"
        name="current-password"
      />
      <div class="error" v-if="this.errors.currentPasswordEmpty">
        Vous devez saisir votre mot de passe !
      </div>
      <div class="error" v-else-if="this.errors.currentPasswordIsWrong">
        Le mot de passe est incorret
      </div>
      <div class="error" v-else>
      </div>
      <InputPassword
        title="Nouveau mot de passe"
        :theClass="this.passwordCheck.isPasswordValid()"
        @new-value="check($event)"
        name="new-password"
      />
      <div class="error" v-if="this.errors.passwordEmpty">
        Vous devez saisir un mot de passe !
      </div>
      <div class="error" v-else-if="this.errors.invalidPassword">
        Votre mot de passe n'est pas assez fort !
      </div>
      <div class="error" v-else>
      </div>
      <PasswordCheckView />
      <InputPassword
        title="Confirmez"
        :theClass="this.passwordCheck.isPasswordValidationOk(passwordVerify)"
        @new-value="recordPasswordValidation($event)"
        name="password-Validation"
      />
      <div class="error" v-if="this.errors.passwordVerifyEmpty">
        Vous devez saisir une deuxième fois votre mot de passe !
      </div>
      <div class="error" v-else-if="this.errors.passwordsDoesNotMatch">
        Les 2 mots de passe ne sont pas identiques !
      </div>
      <div class="error" v-else-if="errorMessage">
        Il y a eu un problème. Message : {{errorMessage}}
      </div>
      <div class="error" v-else>
      </div>
      <div class="modale-buttons">
        
        <button @click="handleChangePassword()" :disabled="this.currentPassword == '' || passwordCheck.password == '' || this.passwordVerify == '' || this.awaiting">
          <span v-if="!this.awaiting">Ok</span>
          <SpinnerCpnt v-else />
        </button>
        <button @click.prevent="closeChangePasswordModal()">Annuler</button>
      </div>
    </form>
    </div>

  </Modal>
  <Modal  @close="toggleChangePasswordIsValidateModal" :modalActive="showChangePasswordIsValidate"
  title="Votre mot de passe a été changé">
    <button
      @click="toggleChangePasswordIsValidateModal"
      style="margin: 1em auto 0;">
      Fermer
    </button>
  </Modal>
  <Modal  @close="userWasDeleted ? closeAndQuit() : toggleDeleteAccountModal()" :modalActive="showDeleteAccountModal"
  title="Supprimer votre compte">
  <div v-if="! userWasDeleted">
    <p>Êtes-vous certain de vouloir supprimer votre compte ? </p>
    <!-- <p>Toutes vos données seront effacées, il n'y aura plus aucune trace de vous dans cette application ! -->
      <!-- </p> -->
    <p>Cette action n'est pas réversible</p>
    <CheckBox
      @toggled="toggleDeleteConfirme()"
      name="ConfirmDeletion"
      :checked="deleteConfirme"
    >
    <p style="display:inline; margin-left: 0.5em; font-size: 1.2em; color:#c00;">Oui je le veux</p>
  </CheckBox>
  <div v-if="errorMessageForDeleteAccount" class="error">{{ errorMessageForDeleteAccount }}</div>
</div>
<div v-else>
  <p>votre compte a bien été supprimé. Vous pouvez fermer cette fenêtre.</p>
</div>

    <div class="modale-buttons">
    <button
    v-if="!userWasDeleted"
    class="delete-btn"
    :disabled = "!deleteConfirme"
    @click="deleteAccount"
    style="margin: 1em auto 0;">
    Supprimer
  </button>
  <button
    @click="userWasDeleted ? closeAndQuit() : toggleDeleteAccountModal()"
    style="margin: 1em auto 0;">
    Fermer
  </button>
  </div>

  </Modal>
    <h2>Mon profil</h2>
  
  <LoaderView v-if ="! this.userStore.user"/>
  <div class="page" v-else>
    <div class="container">
      <div class="left">
        <p v-if="this.date">Enregistré le : <br>
        {{ date.getDate() }} {{ getReadableMonth() }} {{ date.getFullYear() }}</p>
        <p>Identifiant : <br>{{ this.userStore.user['username'] }}</p>
        <p>Nom d'affichage :  <br>{{ this.userStore.user['slug'] }}</p>
        <p v-if=" this.userStore.user['last_name'] != ''">Nom :  <br>{{ this.userStore.user['last_name'] }}</p>
        <p v-if=" this.userStore.user['first_name'] != ''">Prénom :  <br>{{ this.userStore.user['first_name'] }}</p>
      </div>
      <div class="right">
        <img :src="this.userStore.user['avatar_urls']['96']" alt="Avatar">
        <p class="mini">créez votre avatar sur <a href="https://gravatar.com" target="blank">gravatar.com</a></p>
      </div>
    </div>
    
    <p>E-mail : <br>{{ this.userStore.user['email'] }}</p>
    
    <p
      class="link"
      @click="toggleChangePasswordModal()"
      >
      modifier mon mot de passe
    </p>
    
    <p class="link red"
    @click="toggleDeleteAccountModal"
    >supprimer mon compte</p>
  </div>
</template>

<script>
import { useUserStore } from '@/stores/User';
import { userService } from '@/services/userService'
import LoaderView from '@/components/Layout/Loading.vue';
import InputPassword from '@/components/InputPassword.vue';
import  {usePasswordCheckStore} from '@/stores/PasswordChecking';
import SpinnerCpnt from '@/components/SpinnerCpnt.vue'
import CheckBox from '@/components/InputCheck.vue'
import PasswordCheckView from '@/components/PasswordCheckView.vue'
import Modal from '@/components/Modal.vue'
import {ref} from 'vue'
import router from '@/router'


export default{
  name:'ProfilView',
  
  components: {
    LoaderView,
    InputPassword,
    SpinnerCpnt,
    PasswordCheckView,
    Modal,
    CheckBox,
  },
  
  async created() {
    if ( await userService.isConnected() ){
      this.userStore.createUser(await userService.getUserInfos())
      this.date = new Date(this.userStore.user['registered_date'])
    }
    else{
      this.$router.push( {name: '403'})
    }
  },

  setup() {
    const date = ref(false)
    const getReadableMonth = () => {
      switch (date.value.getMonth()) {
        case 1 : {
          return 'janvier'
        }
        case 2 : {
          return 'février'
        }
        case 3 : {
          return 'mars'
        }
        case 4 : {
          return 'avril'
        }
        case 5 : {
          return 'mai'
        }
        case 6 : {
          return 'juin'
        }
        case 7 : {
          return 'juillet'
        }
        case 8 : {
          return 'août'
        }
        case 9 : {
          return 'septembre'
        }
        case 10 : {
          return 'octobre'
        }
        case 11 : {
          return 'novembre'
        }
        case 12 : {
          return 'décembre'
        }
        default: {
          return 'mois'
        }
      }
    }
    const userStore = useUserStore()
    const passwordCheck = usePasswordCheckStore()
    
    const showChangePasswordModal = ref(false)
    const closeChangePasswordModal = () => {
      toggleChangePasswordModal()
      let allInputs = document.querySelectorAll('input')
      for (const input of allInputs){
        input.value = ''
      }
      passwordCheck.password = '';
    }
    
    const toggleChangePasswordModal = () => {
      showChangePasswordModal.value = !showChangePasswordModal.value;
    }
    
    const showChangePasswordIsValidate = ref(false)
    const toggleChangePasswordIsValidateModal = () =>{
      showChangePasswordIsValidate.value = !showChangePasswordIsValidate.value;
    }
    
    const showDeleteAccountModal = ref(false)
    const toggleDeleteAccountModal = () =>{
      showDeleteAccountModal.value = !showDeleteAccountModal.value;
      deleteConfirme.value = false;
    }
    
    const deleteConfirme = ref(false)
    const toggleDeleteConfirme = () => {
      deleteConfirme.value = !deleteConfirme.value;
      console.log(deleteConfirme.value)
    }
    
    const userWasDeleted = ref(false);
    const errorMessageForDeleteAccount = ref(false)
    const deleteAccount = async() => {
      console.log('coucou')
      errorMessageForDeleteAccount.value = false
      if (deleteConfirme.value){
        const data = await userService.deleteAccount()
        
        if (data.success === 1){
          userWasDeleted.value = true
        }
        else{
          errorMessageForDeleteAccount.value = 'Il y a eu une erreur. Message : ' + data.response.message
        }
        
      }
      else{
        errorMessageForDeleteAccount.value = "Vous devez cocher la case"
      }
    }
    const closeAndQuit = () => {
      userStore.userDisconnection()
          toggleDeleteAccountModal()
          router.push({name: 'home'})

    }
        
    return{
      date, getReadableMonth,
      userStore,
      passwordCheck,
      showChangePasswordModal, closeChangePasswordModal, toggleChangePasswordModal,
      showChangePasswordIsValidate, toggleChangePasswordIsValidateModal,
      showDeleteAccountModal, toggleDeleteAccountModal,
      errorMessageForDeleteAccount, deleteConfirme, toggleDeleteConfirme,
      deleteAccount, closeAndQuit, userWasDeleted,
    }
  },

  data() {
    return{

      currentPassword:'',
      passwordVerify: '',
      
      errors : {
        
        currentPasswordEmpty: false,
        passwordEmpty: false,
        passwordVerifyEmpty: false, 
        invalidPassword:false,
        passwordsDoesNotMatch:false,
        currentPasswordIsWrong:false,
				},
      
      awaiting:false,
      errorMessage:'',
      //success:false,
    }
  },
  

  methods : {
  
    
    check(value){
      this.passwordCheck.updatePassword(value)
    },
    recordPasswordValidation(value){
      this.passwordVerify = value
    },
    recordCurrentPwd(value){
      this.currentPassword = value  
    },
    
    async handleChangePassword()
    {
      this.errors.invalidPassword = false;
      this.errors.currentPasswordIsWrong = false
      
      // --- Vérification des données du formulaire --- //
      // En version raccourcie : on stocke le résultat de la condition dans la variable

      this.errors.currentPasswordEmpty  = ( this.currentPassword == "" );
      this.errors.passwordEmpty  = ( this.passwordCheck.password == "" );
      this.errors.passwordsDoesNotMatch = ( this.passwordCheck.password != this.passwordVerify )
      this.errors.invalidPassword = ( !this.passwordCheck.regPassword.test(this.passwordCheck.password) );
            
      // Si une erreur est rencontrée, on n'envoi pas la requete au serveur !
      if( !this.errors.currentPasswordEmpty &&
          !this.errors.passwordEmpty &&
          !this.errors.passwordVerifyEmpty &&
          !this.errors.passwordsDoesNotMatch &&
          !this.errors.invalidPassword &&
          !this.currentPasswordIsWrong)
      {

        this.awaiting = true;

        const data = await userService.passwordResetFromProfil( this.currentPassword, this.passwordCheck.password )
        
        if (data.success == 1){
          this.toggleChangePasswordModal()
          this.awaiting = false
          this.errors.currentPasswordEmpty = false
          this.errors.passwordEmpty = false
          this.errors.passwordVerifyEmpty = false
          this.errors.invalidPassword = false
          this.errors.passwordsDoesNotMatch = false
          this.currentPassword = ''
          this.passwordVerify = ''
          this.passwordCheck.updatePassword('')
          let allInputs = document.querySelectorAll('input')
          for (const input of allInputs){
            input.value = ''
          }
          setTimeout(()=>{this.toggleChangePasswordIsValidateModal()}, 100)
        }
      
        else{
          this.awaiting = false
          //console.log(data.response.message == 'Password is wrong')
          if (data.response.message == 'Password is wrong')
            this.errors.currentPasswordIsWrong = true
          else
            this.errorMessage = data.response.data.message          
        }
      }
    }
  },

}
</script>

